/* --------------------- 
BASIC SETTINGS
--------------------- */
body {font-family: 'Raleway', sans-serif;}
a {color: #000;}

a:hover {
    color: #f53f2c;
    text-decoration: none;
    -webkit-transition: .5s ease-out;
    -o-transition: .5s ease-out;
    transition: .5s ease-out;
}

a:visited{
    text-decoration: none
}

.highlight {color: #f53f2c;}
h1 {font-size: 138px;}
h2 {font-family: 'Pacifico', cursive;}
h4 {font-weight: 600;}

p {
    font-size: 13px;
    line-height: 1.7em;
    color: #b2b2b2;
}

/* --------------------- 
BUTTONS
--------------------- */
.btn:focus,
input:focus {
    outline: none !important;
}

.btn {
    border-radius: 10px;
    padding: 5px 10px;
    font-weight: 600;
    -webkit-transition: .5s ease-out;
    -o-transition: .5s ease-out;
    transition: .5s ease-out;
}

.btn:focus {
    background: #b73426;
    color: #fff;
    border: 2px solid #f53f2c;
}

.btn-default {
    background: none;
    color: #f53f2c;
    border: 2.5px solid #f53f2c;
}

.btn-default:hover {
    background: #f53f2c;
    color: #fff;
    border: 2px solid #b73426;
}

.btn-reverse {
    border: 2.5px solid #fff;
    color: #fff;
    border-radius: 0;
}

.btn-reverse:hover {
    color: #fff;
    border-radius: 45px;
}

.btn-inverse {
    background: #f53f2c;
    color: #fff;
    border: 2.5px solid #f53f2c;
    margin-top: 0;
}

.btn-inverse:hover {
    background: #b73426;
    color: #fff;
    border: 2.5px solid #f53f2c;
}

/* --------------------- 
INPUT FIELDS
--------------------- */
input {
    padding: 10px 30px;
    border-radius: 10px;
    border: 2px solid #b2b2b2;
    margin: 30px 5px;
    color: #b2b2b2;
    -webkit-transition: .5s ease-out;
    -o-transition: .5s ease-out;
    transition: .5s ease-out;
}

input:focus {
    border: 2px solid #f53f2c;
}

/* --------------------- 
SECTION HEADINGS
--------------------- */
.sectionhead {padding: 0 150px;}
.sectionhead h2 {padding-bottom: 10px;}

.sectionhead p {
    font-size: 16px;
    line-height: 1.6em;
}

.sectionhead hr {
    width: 75px;
    border-bottom: 4px solid #eeeeee;
}

/* --------------------- 
OVERVIEW SECTION
--------------------- */
#overview {
    text-align: center;
    padding-top: 100px;
}

#overview .col-md-6,
#overview .col-lg-4 {
    padding-top: 50px;
}

#overview img {height: 120px;}

/* --------------------- 
CALL-TO-ACTION SECTION
--------------------- */
.calltoaction {
    margin-top: 75px;
    background: url(../img/bg.jpg) fixed;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.calltoaction .content {padding: 65px 0;}
.calltoaction p {color: #9f9f9f;}

.calltoaction img {
    margin-top: -60px;
    margin-bottom: -15px;
}

/* --------------------- 
SUBSCRIBE SECTION
--------------------- */
#subscribe {
    margin-top: 155px;
    background: url(../img/bg.jpg) fixed;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    color: #fff;
}

.santaicon {
    margin-top: -160px;
    overflow: hidden;
}

#subscribe .container {padding: 65px 0;}

/* --------------------- 
FOOTER SECTION
--------------------- */
footer {padding: 75px 0 30px 0;}
.logo {margin-top: -20px;}

.santacap {
    margin-top: -110px;
    margin-left: -105px;
}

footer hr {
    width: 75px;
    border-bottom: 4px solid #eeeeee;
}

/* --------------------- 
OVERLAY PRESET
--------------------- */
.redoverlay {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 24, 0, 0.75);
    position: relative;
}

.lightoverlay {
    background-color: rgba(248, 248, 248, 0.9);
    position: relative;
}

.darkoverlay {
    background-color: rgba(54, 57, 66, 0.75);
    position: relative;
}

/* --------------------- 
SOCIAL ICONS
--------------------- */
.sticky-container {
    position: fixed;
    right: -125px;
    top: 130px;
    width: 200px;
}

.sticky li {
    list-style: none;
    background-color: #333;
    color: #b2b2b2;
    height: 36px;
    margin: 0px 0px 3px 0px;
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    cursor: pointer;
    filter: url("data:image/svg+xml;utf8,<svg xmlns=\'https://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'saturate\' values=\'0.5\'/></filter></svg>#grayscale");
    filter: gray alpha(opacity=50);
    -webkit-filter: grayscale(50%);
}

.sticky li:hover {
    margin-left: -115px;
    filter: none;
    -webkit-filter: grayscale(0%);
}

.sticky li img {
    float: left;
    width: 36px;
    height: 36px;
    margin-right: 15px;
    padding: 2px;
}

.sticky li p {
    text-transform: uppercase;
    line-height: 36px;
}


